import store from './components/redux/store';

let obj = {
    deleteAd:(id)=>{

        fetch('/delete_ad?cid='+id, {
            method:'DELETE'            
        }).then((resp)=>{

            return resp.json();

        }).then((resp)=>{

            if(resp.ok){

                store.dispatch({
                    type:"AD_DELETED",
                    payload:id
                });

                alert("deleted");
            }

        })

    },
    createAd: (data) => {

        fetch('/post_ad', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }).then((resp) => {

            return resp.json();

        }).then((resp) => {

            if (resp.ok) {
                alert("Ad posted");
            }

        })

    },
    logout:(data)=>{

        fetch('/logout', {
            method:'POST'
        }).then(resp=>resp.json()).then((resp)=>{

            if(resp.success){
                store.dispatch({
                    type:"USER_LOGGED_OUT"
                })
            }

        })


    },
    login: (data) => {

        
        fetch('/login', {
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        }).then((resp)=>{

            resp.json().then((resp)=>{
                
                if(resp._id){
                    alert("User found")
                    store.dispatch({
                        type:'USER_LOGIN_SUCESS',
                        payload:resp
                    });;
                }

            })

        }).catch((resp)=>{
            console.log(resp); 
        });

    },
    signup: (data) => {

        fetch('/signup', {
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        }).then((resp)=>{

            resp.json().then((resp)=>{
                
                if(resp.success){
                    alert("User created")
                }

            })

        }).catch((resp)=>{
            console.log(resp); 
        });

    }
}

export default obj;